import React from 'react';
import styled from 'styled-components';
import { Container } from '@components/global';
import Call2Act from '@ui/Call2Act';
import Fade from 'react-reveal/Fade';

const SectionHeader = ({ header, subheader, text, href, bg, toggle2Act, target }) => (
  <Container>
    <HeaderWrapper accent={bg}>
      <Fade>
        <h2>{header}</h2>
        <br/>
        <p>{subheader}</p>
        <Toggle2Act toggle2Act={toggle2Act}>
          <Call2Act text={text} href={href} accent={bg} target={target}/>
        </Toggle2Act>
      </Fade>
    </HeaderWrapper>
  </Container>
);


const HeaderWrapper = styled.div`
  padding: 65px 0 80px 0;
  margin-left: 0%;
  max-width: 60%;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S1000};
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 15px 0;
    margin-left: 0%;
  }

  ${props =>
    props.accent &&
    `color: ${
      props.accent === 'inverse'
        ? props.theme.color.white.regular
        : props.theme.color.grayscale.S1000
    }`};

  p {
    ${props =>
      props.accent &&
      `color: ${
        props.accent === 'inverse'
          ? props.theme.color.grayscale.S500
          : props.theme.color.grayscale.S1000
      }`};
  }
`;

const Toggle2Act = styled.div`
  display: none;
  ${props =>
    props.toggle2Act &&
    `display: ${
      props.toggle2Act === 'false'
        ? 'none'
        : 'block'
    }`};
  }
`;

export default SectionHeader;
